* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.calendar {
    display: flex;
    flex-direction: column;
    justify-content: start;
    min-height: 600px;
    background: #5E4220;
    text-align: center;
    border-radius: 10px;
    padding-bottom: 32px;
}