:root {
    --primary : #D7CAB6;
}

.button {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}

.button-primary{
    background-color: var(--primary);
    color: #2E2821;
    border: 1px solid var(--primary);
}

.button-outline {
    background-color: transparent;
    color: #D7CAB6;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.button-medium {
    padding: 8px 20 px;
    font-size: 18px;
}

.button-large{
    padding: 12px 26px;
    font-size: 20px;
}

.button--mobile{
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #D7CAB6;
    padding: 14px 20px;
    border: 1px solid #D7CAB6;
    transition: all 0.3s ease-out;
}

.button--wide { 
    padding: 12px 64px;
    font-size: 20px;
}

.button--large:hover, .button-medium:hover, .button--mobile:hover {
    transition: all 0.3s ease-out;
    background: #D7CAB6;
    color:#2E2821;
}

.button--wide:hover{
    color: #D7CAB6;
    background-color: #9A7F5B;
    transition: all 0.2s ease-out;
}

.blue{
    background-color: #8A642E;
    color: #D7CAB6;
    border: none;
}

.red{
    background-color: #9A7F5B;
    color: #D7CAB6;
    border: none;
}

.primary{
    background-color: #D7CAB6;
    color: #242424;
    border: none;
}

.primary:hover {
    background-color: #242424;
    color: #D7CAB6;
    border: none;
}

.green{
    background-color: #25ce4a;
    color: #D7CAB6;
    border: none;
}

.green:hover {
    background-color: #242424;
}

.button-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height:100px;
    width: 100;
}