* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
        'Lucida Sans', Arial, sans-serif;
}

.TK {
    display: flex;
    flex-direction: column;
    justify-content: start;
    min-height: 600px;
    background: #5E4220;
    text-align: center;
    border-radius: 10px;
    padding-bottom: 32px;
}

h1 {
margin: 32px 0;
color: #D7CAB6;
font-size: 24px;
}

.complete {
text-decoration: line-through;
opacity: 0.4;
}

.todo-form {
    margin-bottom: 32px;
}

.todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #897C2D;
    outline: none;
    width: 320px;
    background: transparent;
    color: #D7CAB6;
}

.todo-input::placeholder {
color: #D7CAB6;
}

.todo-button {
padding: 16px;
border: none;
border-radius: 0 4px 4px 0;
cursor: pointer;
outline: none;
background: linear-gradient(
    90deg,
    #897C2D 0%,
    #202C17 100%
);
color: #D7CAB6;
text-transform: capitalize;
}

.todo-input.edit {
border: 2px solid #95A26F;
}

.todo-button.edit {
background: linear-gradient(
    90deg,
    #568293 0%,
    #E3DC95 100%
);
padding: 16px 22px;
}

.todo-container {
display: flex;
flex-direction: row;
position: relative;
}

.todo-row {
display: flex;
justify-content: space-between;
align-items: center;
margin: 4px auto;
color: #373916;
background: linear-gradient(
    90deg,
    #D8CA91 0%,
    #897831 100%
);

padding: 16px;
border-radius: 5px;
width: 90%;
}

.todo-row:nth-child(4n + 1) {
background: linear-gradient(
    90deg,
    #897C2D 0%,
    #202C17 100%
);
}

.todo-row:nth-child(4n + 2) {
background: linear-gradient(
    90deg,
    #94A288 0%,
    #415040 100%
);
}

.todo-row:nth-child(4n + 3) {
background: linear-gradient(
    90deg,
    #5E8793 0%,
    #95A26F 100%
);
}

.icons {
display: flex;
align-items: center;
font-size: 24px;
cursor: pointer;
}

.delete-icon {
margin-right: 5px;
color: #D7CAB6;
}

.edit-icon {
color: #D7CAB6;
}